$cornerRadius: 20px;

.game {
    background-color: white;
    padding: 30px 20px 40px;
    border-radius: $cornerRadius;
    width: 1200px;
    position: relative;
    display: flex;
    gap: 3em;

    .map {
        background-color: #fdf5e0;
        width: 66%;
        border-radius: $cornerRadius;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 20px 0 30px;

        .header {
            position: relative;
            padding: 0 20px;
            width: 100%;
            z-index: 1;

            .timer {
                position: absolute;
                right: -18px;
                top: 30px;
                width: 357px;
                height: 65px;
                div {
                    color: white;
                    line-height: 45px;
                    font-size: 18px;
                    margin-left: 20px;
                    b {
                        margin-right: .5em;
                    }
                }
            }

            .cards {
                margin: 20px 0;
                display: flex;
                gap: 1em;
                justify-content: space-between;

                .card {
                    border-radius: $cornerRadius;
                    background-color: white;
                    padding: 10px 15px;
                    display: flex;
                    align-items: center;
                    gap: 20px;
                    transition: all ease .3s;
                    border: 1px solid rgba(255, 220, 94, 0);
                    cursor: pointer;

                    &:hover, &:active, &:focus {
                        border: 1px solid #ffdc5e;
                        box-shadow: 0 0 5px #ffdc5e;
                    }

                    .image {
                        img {
                            width: 100px;
                            animation: rotate infinite 1s alternate;
                        }
                    }

                    .info {
                        font-size: 16px;
                        color: black;
                        min-width: 160px;
                        .title {
                            font-weight: bold;
                        }
                        strong {
                            font-size: 2em;
                            padding: 0 5px;
                            font-weight: bold;
                        }

                        .milestone {
                            .number {
                                font-weight: bold;
                                font-size: 1.4em;
                            }
                            .bar {
                                width: 160px;
                                height: 15px;
                                border: 1px solid #ffb3b3;
                                border-radius: 20px;
                                overflow: hidden;
                                .value {
                                    height: inherit;
                                    background: rgb(255,101,49);
                                    background: linear-gradient(90deg, rgba(255,101,49,1) 0%, rgba(255,25,25,1) 100%);
                                }
                            }
                        }
                        .button {
                            display: flex;
                            align-items: flex-end;
                        }
                    }

                }
            }
        }

        .body {
            cursor: pointer;
            padding: 0 20px;
            img {
                margin: 0 auto;
            }
        }
    }

    .sidebar {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 25%;
        padding: 0 20px;

        img {
            width: 200px;
            animation: move infinite .5s alternate;
        }

        hr {
            border-top: 1px dotted #ff2c1e;
            width: 100%;
        }
    }
}

@keyframes move {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-30px);
  }
}

@keyframes rotate {
  from {
    transform: rotate(-5deg) scale(0.9);
  }
  to {
    transform: rotate(5deg) scale(1.02);
  }
}